search-box
{
    width:80% ; 
    margin: 0 0 75px ;
}

.search-box .search-bar
{
    display: block;
    width: 50%;
    padding: 15px;

    -webkit-appearance: none;

       -moz-appearance: none;

            appearance: none;
    background: none ; 
    border: none ;
    outline: none ;

    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: 10px;
    margin-left: 20% ; 


    box-shadow: 2px 5px rgb(7, 7, 7);

    color: #313131;
    font-size: 20px;

    transition: 0.4s ease ;
}
.search-box .search-bar:focus{
    background-color: rgba(255, 255, 255, 0.75);
}


