.fixed 
{
	 background-color: #8f8484;
	 position: fixed;
	 border-color: #eeeeee;
	 -webkit-animation: navfix .5s ease ;
	         animation: navfix .5s ease ;
	 z-index: 11;
}
@-webkit-keyframes navfix
{
	0%
	{
		-webkit-transform: translateY(-100%);
		        transform: translateY(-100%);
	}
	100%
	{
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
}
@keyframes navfix
{
	0%
	{
		-webkit-transform: translateY(-100%);
		        transform: translateY(-100%);
	}
	100%
	{
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
}

.brand-name .fa-logo 
{
	text-decoration: none ;
	font-size: 45px ;
	font-weight: 600 ; 
	color: #345CDB; 
	position: relative;
    font-family: 'Source Code Pro', monospace;
    position: relative;
    left:20px;
    cursor: pointer;

}


.navbar ul
{
	list-style: none ;
	padding: 0;
	margin: 0;
}
.navbar ul li 
{
	display: inline-block;
	margin-left: 35px ;
}

.navbar ul li a 
{
	font-size: 15px ;
	text-decoration: none ;
	line-height: 72px ;
	color: #8e16bd;
	/*color: var(--text-color);*/
	display: block;
	font-weight:600;
	position: relative;
}

.navbar ul li a::before
{
	content: '';
	height: 3px ;
	background-color: #345CDB;;
	width: 0%;
	position: absolute;
	right: 0px ;
	bottom: -1px ;	
	transition: all .5s ease ;
}

header.fixed .navbar ul li a::before
{
	background-color: #345cdb;
}


.navbar ul li a:hover::before
{
	width: 100%;
	left:0; 
}
.navbar ul li a.active::before
{
	width: 100%;
	left:0; 
}
