@import url('https://fonts.googleapis.com/css?family=Ubuntu:300,500,700');
@import url("https://use.fontawesome.com/releases/v5.13.0/css/all.css");
@import url("http://weloveiconfonts.com/api/?family=entypo");

body{
  background: #242539;
  font-family: 'Ubuntu', sans-serif;
}

.wrapper{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display:flex;
}

.contain{
  width: 300px;
  height: 420px;
  border-radius: 10px;
  background: #fff;
}

.img-container{
  width: 100%;
  height: 40%;
  position: relative;
}

.img-container img.banner-img{
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.img-container img.profile-img{
  position: absolute;
  bottom: -50px;
  left: 100px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 5px solid #fff;
}

.share{
  position: absolute;
  top: 10px;
  right: 10px;
}

.share ul{
  list-style: none;
}

.share ul li{
  width: 35px;
  height: 35px;
  margin-bottom: 5px;
  border-radius: 50%;
  border: 2px solid #673BB7;
  text-align: center;
  cursor: pointer;
  transition: all 1s ease;
}

.share ul li .fa{
  line-height: 40px;
  color: #673BB7;
  position: relative;
  top: 5px;
  transition: all 1s ease;
}

.share ul li:hover{
  background: #673BB7;
}

.share ul li:hover .fa{
  color: #fff;
}

.content{
  height: 60%;
  width: 100%;
  box-sizing: border-box;
  padding: 40px 20px 40px;
  position: relative;
}

.title{
  text-align: center;
  color: #673BB7;
  padding-bottom: 0px;
}

.title p{
  font-size: 28px;
  font-weight: 700;
}

.title .text{
  font-size: 12px;
  font-weight: 400;
}


.content p.location{
  display: block;
  margin: 5px 0 20px;
}

.content span.location .fa{
  font-size: 18px;
  margin-right: 5px;
}



.content data{
  width: 100%;
  overflow: hidden;
  margin: 30px 0 20px;
}

.inner-data{
  width: 48.3%;
  float: left;
  text-align: center;
}

.inner-data span .fa{
  font-size: 20px;
  margin-bottom: 5px;
  color: #673BB7;
}


.inner-data span FontAwesomeIcon{
  font-size: 30px;
  margin-bottom: 5px;
  color: #673BB7;
}


.follow{
  position: relative;
  text-align: center;
  border: 2px solid #673BB7;
  border-radius: 50px;
  padding: 10px;
  color: #673BB7;
  background-color: white;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 1s ease;
  left: 90px;
}

.follow:hover{
  color: #fff;
  background: #673BB7;
}

.heart,
.like{
  cursor: pointer;
}

.heart:before{
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-left: 30px solid #673BB7;
  border-right: 30px solid transparent;
  border-top: 30px solid transparent;
  border-bottom: 30px solid #673BB7;
  border-bottom-left-radius: 8px;
}

.heart:after{
  font-family: "Font Awesome 5 Free";
  font-size: 20px;
  content:'\f584';
  position: absolute;
  bottom: 10px;
  left: 5px;
  color: #fff;
}
.heart:hover::after{
  font-family: "Font Awesome 5 Free";
  font-size: 20px;
  content:'\f584';
  position: absolute;
  bottom: 10px;
  left: 5px;
  color: rgb(104, 29, 29);
}

.like:before{
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid #673BB7;
  border-top: 30px solid transparent;
  border-bottom: 30px solid #673BB7;
  border-bottom-right-radius: 8px;
}

.like:after{
  font-family: "Font Awesome 5 Free";
  content: '\f004'; 
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: #fff;
  font-weight: 300;
}
.like:hover::after{
  font-family: 'entypo', sans-serif;
  content: '\2665'; 
  position: absolute;
  bottom: 10px;
  right: 10px;
  color:wheat;
  font-weight: 500;
}

input[type="checkbox"]
{
  display: none;
  
}

